import geoUtil from "../utils/geoUtil.js";
self.addEventListener("message", (event) => {
  const { type, data } = event.data;
  const handlers = {
    generateBuilding: (data) => {
      return geoUtil.generateBuilding(data);
    },
    generateWater: (data) => {
      return geoUtil.generateWaterAttr(data);
    },
    generateTerrain: (data) => {
      return geoUtil.generateTerrain(data);
    },
    generateRoadAttr: (data) => {
      return geoUtil.generateRoadAttr(data);
    },
  };
  const result = handlers[type] && handlers[type](data);
  postMessage(result);
  close();
  return;
});
