const WorldRadius = 6378137;
function threeToGis(t) {
  return [
    (((180 + t[0]) % 360) / 360) * (2 * Math.PI * WorldRadius),
    ((180 -
      (180 / Math.PI) *
        Math.log(Math.tan(Math.PI / 4 + (t[1] * Math.PI) / 360))) /
      360) *
      (2 * Math.PI * WorldRadius),
  ];
}
function diffPosition(t, e) {
  return {
    x: e[0] - t[0],
    y: t[1] - e[1],
  };
}
// gisthree
const centralPositions = (center, gisCenter, type, paths) => {
  const pos = [];
  switch (type) {
    case "earth":
      break;
    case "gis":
      for (let n = 0; n < paths.length; n++) {
        const curPos = paths[n];
        if (Array.isArray(curPos[0])) {
          const curPosList = [];
          for (let i = 0; i < curPos.length; i++) {
            curPosList.push(
              curPos[i].map((p) => {
                const gisPos = threeToGis([p.x, p.y]);
                const diffPos = diffPosition(gisCenter, gisPos);
                return new THREE.Vector3(diffPos.x, diffPos.y, p.z);
              }),
            );
          }
          pos.push(curPosList);
        } else {
          pos.push(
            curPos.map((p) => {
              const gisPos = threeToGis([p.x, p.y]);
              const diffPos = diffPosition(gisCenter, gisPos);
              return new THREE.Vector3(diffPos.x, diffPos.y, p.z);
            }),
          );
        }
      }
      break;
    case "normal":
      for (let n = 0; n < paths.length; n++) {
        const curPos = paths[n];
        if (Array.isArray(curPos[0])) {
          const curPosList = [];
          for (let i = 0; i < curPos.length; i++) {
            curPosList.push(
              curPos[i].map((p) => {
                const diffPos = diffPosition(center, [p.x, p.y]);
                return new THREE.Vector3(diffPos.x, diffPos.y, p.z);
              }),
            );
          }
          pos.push(curPosList);
        } else {
          pos.push(
            curPos.map((p) => {
              const diffPos = diffPosition(center, [p.x, p.y]);
              return new THREE.Vector3(diffPos.x, diffPos.y, p.z);
            }),
          );
        }
      }
      break;
    default:
      console.warn("no such type in engine".concat(type));
      break;
  }
  return pos;
};
// line
const formateLineData = (paths) => {
  let pos = [];
  for (let i = 0; i < paths.length; i++) {
    const type = paths[i].geometry && paths[i].geometry.type;
    if ("MultiLineString" === type) {
      const coordinates = paths[i].geometry.coordinates;
      for (let n = 0; n < coordinates.length; n++) {
        const coordinate = coordinates[n];
        if (1 === coordinate.length) {
          const coor = coordinate[0];
          const list = [];
          for (const j = 0, len = coor.length; j < len; j++) {
            list.push(new THREE.Vector3(coor[j][0], coor[j][1], 0));
          }
          pos.push(list);
        } else {
          const list = [];
          for (let j = 0, len = coordinate.length; j < len; j++) {
            const coor1 = coordinate[j];
            list.push(new THREE.Vector3(coor1[0], coor1[1], 0));
          }
          pos.push(list);
        }
      }
    } else if ("LineString" === type) {
      const coordinates = paths[i].geometry.coordinates;
      if (1 === coordinates.length) {
        const coors = coordinates[0];
        const list = [];
        for (let j = 0; j < coors.length; j++) {
          list.push(new THREE.Vector3(coors[j][0], coors[j][1], 0));
        }
        pos.push(list);
      } else {
        const list = [];
        for (let z = 0; z < coordinates.length; z++) {
          const coors = coordinates[z];
          list.push(new THREE.Vector3(coors[0], coors[1], 0));
        }
        pos.push(list);
      }
    } else if (!type) {
      let coordinates = paths[i];
      for (let j = 0; j < coordinates.length; j++) {
        let list = [];
        for (let coors = coordinates[j], m = 0; m < coors.length; m++) {
          list.push(coors[m]);
        }
        pos.push(list);
      }
    }
  }
  return pos;
};
